import Footer from "./components/Footer";
import { useEffect } from "react";
import Response from "./components/response";
import Nav from "./components/Nav";
import NavMobile from "./components/NavMobile";
import "./App.css";
import Missions from "../src/dataJson/mission1.json";

function App() {
  const check = () => {
    let checkbox = document.querySelectorAll("[type=checkbox]");
    let checkboxArray = Array.from(checkbox);
    let correct = 0;
    for (let i = 0; i < checkboxArray.length; i++) {
      if (checkboxArray[i].checked === true) {
        if (
          checkboxArray[i].name ===
            `_${Missions.thirdMission.rightAnswersIndices[0]}` ||
          checkboxArray[i].name ===
            `_${Missions.thirdMission.rightAnswersIndices[1]}`
        ) {
          correct += 1;
        }
      }
    }
    console.log("correct number", {correct:correct});
    answer(correct);
  };
  const answer = (correct) => {
    if (correct === 0) {
      document.querySelector(".an0").style.display = "block";
    } else if (correct === 1) {
      document.querySelector(".an1").style.display = "block";
    } else if (correct === 2) {
      document.querySelector(".an2").style.display = "block";
    }
  };

  const times = () => {
    let checkbox = document.querySelectorAll("[type=checkbox]");
    let checkboxArray = Array.from(checkbox);

    let selected = 0;
    for (let i = 0; i < checkboxArray.length; i++) {
      if (checkboxArray[i].checked === true) {
        selected += 1;
      }
    }
    if (selected !== 2) {
      document.querySelector("#submit").removeEventListener("click", check);
    } else {
      document.querySelector("#submit").addEventListener("click", check);
    }
  };

  useEffect(() => times());

  return (
    <main>
      <Nav />
      <NavMobile />
      <div className="header">
        <h1>سطو من الداخل</h1>
      </div>
      <div className="welcome">
        <h2 style={{ color: "#c22735" }}>مرحباً أيها المحقق</h2>
        <p>{Missions.thirdMission.title}</p>

        <p>{Missions.thirdMission.subtitle}</p>
      </div>

      <div className="main_container">
        <div className="documents">
          {Missions.thirdMission.clues.map((doc, id) => (
            <label key={`___${id}`}>
              <input
                className="checkbox"
                type="checkbox"
                name={`_${id}`}
                key={id}
                onClick={(e) => {
                  e.target.parentNode.classList.toggle("red");
                  times();
                }}
              />
              <span key={`__${id}`} className="checkmark" id={`_${id}`}></span>
              <span className="selection">{doc}</span>
            </label>
          ))}
        </div>
        <div className="btns_container">
          <input id="submit" type="button" value="تحقق" className="submit" />
          <input
            type="button"
            value="تلميح"
            className="submit hint_home"
            onClick={() => {
              document.querySelector(".answer").style.display = "none";
              document.querySelector(".an0").style.display = "none";
              document.querySelector(".hint1").style.display = "block";
            }}
          />
        </div>
      </div>
      <Footer />

      <Response mission={Missions.thirdMission} />
    </main>
  );
}

export default App;
